// ** React Imports
import { useEffect, useState } from 'react'

// ** Third Party Components
import axios from '@src/configs/axios/axiosConfig'
import { Bar } from "react-chartjs-2"

// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  CardTitle,
  DropdownToggle,
  UncontrolledButtonDropdown,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'
import moment from 'moment'

const RevenueReport = () => {
  // ** State
  const currentMonth = moment().format('M')
  const monthlist = ['Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec', 'Jan', 'Feb', 'Mar']
  const [data, setData] = useState(null)
  const [chartData, setChartData] = useState(null)
  const [currentYear, setCurrentYear] = useState(parseInt(currentMonth) > 3 ? moment().add(1, 'y').format('YYYY') : moment().format('YYYY'))
  const [filter, setFilter] = useState({ id: 2, name: 'This Year' })
  const dateList = [{ id: 1, name: 'Last year' }, { id: 2, name: 'This Year' }]

  const indianFormattedPrice = (val) => {

    const value = String(val)

    let removedValue = ''
    let finalVal = ''
    if (value.includes('.')) {
      removedValue = value.slice(value.indexOf('.'))
      finalVal = value.replace(removedValue, '')
      finalVal = finalVal.toString()
    } else {
      finalVal = value.toString()
    }
    let lastThree = finalVal.substring(finalVal.length - 3)
    const otherNumbers = finalVal.substring(0, finalVal.length - 3)
    if (otherNumbers !== '') lastThree = `,${lastThree}`
    const res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree
    return res + (removedValue === '' ? '.00' : removedValue)
  }

  const options = {
    scales: {
      x: {
        grid: {
          display: false
        }
      },
      y: {
        ticks: {
          autoSkip: true,
          maxTicksLimit: 8
        },
        grid: {
          display: false
        }
      }
    },
    plugins: {
      datalabels: {
        display: true
      },
      legend: {
        display: false
      },
      title: {
        display: false
      }
    }
  }

  const getData = (currentYear) => {

    const params = {
      startdate: moment().set('year', currentYear).subtract(1, 'y').set('month', 3).startOf('month').startOf('d').valueOf(),
      enddate: moment().set('year', (parseInt(currentYear) + 1)).set('month', 2).endOf('month').endOf('d').valueOf()
    }

    axios.post('/taskinvoices/revenuereport', params)
      .then(res => {
        const dataSamp = {
          labels: monthlist,
          datasets: [
            {
              label: "Total",
              backgroundColor: "#7367f0",
              borderColor: "#7367f0",
              data: []
            },
            {
              label: "Received",
              backgroundColor: "#28c76f",
              borderColor: "#28c76f",
              data: []
            }
          ]
        }

        monthlist.forEach((name, i) => {
          const total_data = res.data.result.total.find((obj) => {
            const mname = moment(obj.date).format('MMM')
            return (mname === name)
          })
          const received_data = res.data.result.received.find((obj) => {
            const mname = moment(obj.date).format('MMM')
            return (mname === name)
          })

          dataSamp.datasets[0]['data'][i] = total_data?.amount || 0
          dataSamp.datasets[1]['data'][i] = received_data?.amount || 0
        })

        setChartData(dataSamp)
        setData(res.data.result)
      })
    return () => setData(null)
  }

  useEffect(() => {

    getData(currentYear)

  }, [])

  const applyFilter = (item) => {
    setFilter(item)
    let year
    if (item.id === 2) {
      year = moment().add(1, 'y').format('YYYY')
    } else {
      year = moment().format('YYYY')
    }
    setCurrentYear(year)
    getData(year)
  }


  return data !== null ? (
    <Card className='card-revenue-budget'>
      <Row className='mx-0'>
        <Col className='revenue-report-wrapper p-50 p-lg-2' md='9' xs='12'>
          <div className='d-sm-flex justify-content-between align-items-center mb-1'>
            <CardTitle className='mb-50 mb-sm-0'>Revenue Report</CardTitle>
          </div>
          {chartData &&
            <Bar data={chartData} options={options} width={600} height={235} />
          }
        </Col>
        <Col className='budget-wrapper' md='3' xs='12'>
          <UncontrolledButtonDropdown>
            <DropdownToggle className='budget-dropdown' outline color='primary' size='sm' caret>
              {filter?.name}
            </DropdownToggle>
            <DropdownMenu>
              {dateList.map((item, k) => (
                <DropdownItem className='w-100' key={k} onClick={() => applyFilter(item)}>
                  {item.name}
                </DropdownItem>
              ))}
            </DropdownMenu>
          </UncontrolledButtonDropdown>
          <Row>
            <span className='text-primary'>Total sale</span>
            <h6 className='mb-25'>{indianFormattedPrice(data.total_invoice)}</h6>
          </Row>
          <Row>
            <span className='text-success'>Total Receipts</span>
            <h6 className='mb-25'>{indianFormattedPrice(data.total_received)}</h6>
          </Row>
        </Col>
      </Row>
    </Card>
  ) : null
}

export default RevenueReport
